@use "global" as *;

.mv-swiper{

  &-slide__img {
    width: 100vw;
    height: 100vh;
    position: relative;
  
    &::after{
      position: absolute;
      content: "";
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100vw;
      height: 100vh;
      background-color: $bg-black;
      opacity: 0.2;
    }
  
    &::before {
      content: "";
      display: block;
      padding-top: calc(770 / 375 * 100%);
  
      @include mq("md") {
        padding-top: calc(851 / 1280 * 100%);
      }
    }
  
    & > img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100vw;
      height: 100vh;
      object-fit: cover;
    }
  } 
}